@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Open Sans', sans-serif}*{box-sizing:border-box}ul{list-style-type:none;padding:0}button,.button{border:2px solid;padding:13px 30px;background-color:transparent;color:white;font-weight:600;font-size:16px;text-decoration:none}button.button-yellow,.button.button-yellow{border-color:#faaf40}button.button-borderless,.button.button-borderless{border-color:transparent}.sectionTitle{font-weight:700;font-size:16px}section{background-color:#1e1e1e;min-height:100vh;min-width:176px;padding:50px 0px;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:space-between;font-size:calc(10px + 2vmin);color:white}.titles{margin:0;color:#cecece}.logo{max-width:70vw}.my-work{background-color:#faaf40}.my-work li{margin-bottom:30px}.my-work a{font-size:40px;font-weight:800;color:white;opacity:.4;text-decoration:none}.my-work button{border-color:white}.about-me h2{color:#faaf40}.profile-pic{width:250px}

